<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Page</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
          <router-link to="/admin/pages">
            <v-btn color="error">
              <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
          </router-link>
        </div>
    </div>
    <template>
      <div class="card">
        <div class="card-body my-4">
          <v-form ref="form" v-model="valid" lazy-validation>

            <v-autocomplete label="Column" v-model="formData.column" :items="columns" item-text="columns_name" item-value="id" autocomplete="on" ></v-autocomplete>

            <v-text-field label="Page Name" v-model="formData.page_name"></v-text-field>

            <v-switch v-model="formData.status" label="Active" ></v-switch>

            <!-- <v-switch v-model="formData.page_status" label="Static"></v-switch> -->

            <v-text-field class="mb-2 custom-hint"  label="Page Link" hint="/pagelink" persistent-hint v-model="formData.page_link"></v-text-field>

            <v-btn color="success" class="mr-4" @click="savepage">
              SAVE
            </v-btn>
            <router-link to="/admin/pages">
              <v-btn color="error" class="mr-4" @click="reset">
                CANCEL
              </v-btn>
            </router-link>
          </v-form>
        </div>
      </div>
      <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
        <template v-slot:action="{attrs}">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
    </template>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "CreatePages",
  components: {},
  data (){
    return{
      // showTextField: false,
      switch1: false,
      switch2: false,
      columns:[],
      snackbar: false,
      text: '',
      timeout: '',
      formData:{
        column:'',
        page_name:'',
        status:'',
        page_status:''
      },
    }
  },

  created(){
    this.getColumns();
  },
  methods: {
    // toggleTextField() {
    //   this.showTextField = !this.showTextField;
    // },

    validate() {
      this.$refs.form.validate();
    },

    getColumns(){
      axios.get("get/columns").then( (resp) => {
        this.columns = resp.data.columns
      })
    },
    savepage(){
      
      var data = {
        column_id : this.formData.column,
        page_name : this.formData.page_name,
        page_link : this.formData.page_link,
        status : this.formData.status,
        page_status : this.formData.page_status
      };

      axios.post("save/page",data).then( (resp)=> {
        this.snackbar = true;
        this.text = resp.data.message;
        this.timeout = 3000;
        this.$refs.form.reset();
      })
    }
  },
};
</script>

<style scoped>
.custom-hint ::v-deep .v-messages__message {
  color: red;
}

</style>